'use strict';

var chordDiagram = require('./chord_diagram.js');

var _require = require('./chord_library.js'),
    guitarChordLibrary = _require.guitarChordLibrary;

function addChordToDiv(voiceDiv, chord) {
  voiceDiv.className += ' chord';

  if (guitarChordLibrary.has(chord.toString())) {
    var chordDiagramDiv = document.createElement('div');
    chordDiagramDiv.className = 'diagram';
    var shorthands = guitarChordLibrary.get(chord.toString());
    var svgs = shorthands.map(function (shorthand) {
      return chordDiagram.renderChordDiagram(shorthand);
    }); // TODO: Provide a way to scroll through several chord diagrams.

    chordDiagramDiv.innerHTML = svgs[0];
    voiceDiv.appendChild(chordDiagramDiv);
  } else {
    voiceDiv.className += ' highlight';
  }
}

module.exports = {
  addChordToDiv: addChordToDiv
};